import {
  GET_ALBUMS,
  GET_ALBUM,
  ADD_IMAGE,
  CREATE_ALBUM,
  POPUP_TOGGLE,
  DELETE_ALBUM,
  DELETE_IMAGE
} from "../actions/albums";

export default (state, action) => {
  switch (action.type) {
    case GET_ALBUMS:
      return {
        ...state,
        ...action.payload
      };
    case GET_ALBUM:
      return {
        ...action.payload
      };
    case CREATE_ALBUM:
      return {
        ...action.payload
      };
    case ADD_IMAGE:
      return {
        ...state,
        ...action.payload
      };
    case POPUP_TOGGLE:
      if (state.togglePopup == true) {
        return {
          ...state,
          togglePopup: false
        };
      } else {
        return {
          ...state,
          togglePopup: true,
          imageToDelete: action.payload
        };
      }
    case DELETE_ALBUM:
      return {
        ...action.payload
      };
    case DELETE_IMAGE:
      return {
        ...action.payload
      };
    default:
      return state;
  }
};
